<template>
  <div class="shop">
    <div class="notice" ><i class="el-icon-service"></i>营业时间: 08:00~23:55,00:00~08:00</div>
    <div class="watershop">
        <ul class="shoptab">
          <li v-for="(tab,index) in shoptab" :key="index"><router-link :to="tab.url">{{tab.name}}</router-link></li>
        </ul>
    </div>
    <goods></goods>
    <goods-bottom></goods-bottom>
  </div>
</template>

<script>
import goods from './goods'//better-scroll移动端插件
import goodsBottom from './goodsBottom'
export default {
  name: 'shop',
  components:{
    goods,
    goodsBottom
  },
  data () {
    return {
      shoptab:[
        {
          name:'商品',
          url:'shop'
        },
        {
          name:'评价',
          url:'2'
        },
        {
          name:'商家',
          url:'3'
        },
        {
          name:'客服',
          url:'4'
        }
      ]
    }
  }
}
</script>

<style lang="less">
.shop{
  font-size:30px;
  position: relative;
  .notice{
    width: 100%;
    height:40px;
    line-height: 40px;
    background: #ffc;
    color:#f90;
    font-size: 20px;
    position: fixed;
    top: 0px;
    left: 0;
    right: 0;
    z-index: 99999;
    i{
      padding:0px 10px;
    }
  }
  .watershop{
    position: fixed;
    top: 40px;
    left: 0;
    right: 0;
    z-index: 99999;
    .shoptab{
      height: 60px;
      line-height: 60px;
      width: 100%;
      background: #fff;
      li{
        float: left;
        width: 25%;
        height: 60px;
        line-height: 60px; 
        text-align: center;
        font-size:26px;
        a{
          display:block;
          text-decoration: none;
          color:#333;
          &.router-link-active{
            color:#ff2d4b
          }
        }
        
      }
    }
  }
  
}
</style>



