<template>
  <div class="goods">
    <div class="goodsNav"  :style="{height:fullHeight+'px'}">
        <ul>
          <li class="goodsitem" v-bind:class="{ goodsactive:index==current}" @click="addstyle(index,item)" v-for="(item,index) in goods" :key="index" ><span >{{item.name}}</span></li>
        </ul>
    </div>
    <div class="goodsinfo">
      <ul class="goodsmsglist">
        <li v-for="(item,index) in goods" :key="index">
          <h3 class="title">{{item.name}}</h3>
          <ul class="foodsname">
            <li v-for="(food,index) in item.foods" :key="index">
              <div class="icon">
                <img width="70" height="70" :src="food.icon">
              </div>
              <div class="content">
                <h4 class="name">{{food.name}}</h4>
                <div class="extra">
                  <span class="count">月售{{food.sellCount}}份</span>
                  <span class="">好评率{{food.rating}}%</span>
                </div>
                <div class="price">
                  ￥
                  <span class="now">{{food.price}}</span>
                </div>
              </div>
              <div class="cl"></div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'//better-scroll移动端插件
export default {
  name: 'goods',
  components:{
  },
  data () {
    return {
      current:0,
      fullHeight:document.documentElement.clientHeight-80,
      goods: [],
    }
  },
  methods:{
    addstyle(index,tabNav){
      this.current=index;
    },
  },
  created() {
    let self = this;
    this.$axios.get('../../../static/data.json').then(function (response) {
        self.goods = response.data.goods;
        console.log(response.data.goods)
    }, response => {
      console.log('error,no data');
    });
  },
  watch:{
    fullHeight(val){
      if(!this.timer){
        this.fullHeight = val
        this.timer = true
        let that = this
        setTimeout(function(){
          that.timer = false
        },400)
      }
    }
  },
  mounted(){
    let that = this
    window.onresize=()=>{
      return (()=>{
        window.fullHeight = document.documentElement.clientHeight
        that.fullHeight = window.fullHeight
      })()
    }
  }
}
</script>

<style lang="less">
.goods{
  position: absolute;
  top: 100px;
  left: 0;
  right: 0;
  .goodsNav{
    position: relative;
    overflow: hidden;
    padding: 0px;
    position: relative;
    z-index: 10200;
    width: 133px;
    background:#f8f8f8;
    ul{
      background:#f8f8f8;
    }
    .goodsitem{
      width: 110px;
      text-align: center;
      font-size:24px;
      padding:20px 10px;
      border-left:3px solid #f8f8f8;
      span{
        width: 110px;
        text-align: center;
        font-size:24px;
        display: block;
      }
    }
    .goodsactive{
      border-left:3px solid #ff2d4b;
      color:#ff2d4b;
      background: #fff;
    }
  }
  .goodsinfo{
      position: absolute;
      top: 0px;
      right: 0;
      bottom: 80px;
      left: 0;
      overflow: auto;
      
    .goodsmsglist{
      position: relative;
      padding-left:133px;
      li{
        h3{
          font-size:24px;
          line-height: 60px;
          background: #f8f8f8;
          padding-left: 10px;
          color:#3d4145;
          font-weight: normal;
        }
        .foodsname{
          li{
            position: relative;
            padding:10px;
            border-bottom:1px solid #e0e0e0;
            .icon{
              float: left;
            }
            .content{
              float: left;
              margin-left: 10px;
              line-height: 30px;
              h4{
                font-size:22px;
                font-weight:normal;
              }
              .extra{
                span{
                  font-size:14px;
                  color:#666;
                }
              }
              .price{
                font-size:18px;
                color:#ff2d4b;
                span{
                  
                  color:#ff2d4b;
                }
                
              }
            }
          }
        }
      }
    }
  }
  .cl{
    clear: both;
  }
}
</style>



